@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap');
:root {
  --lightgray: #495C58;
  --lightergray: #92A5A1;
  --gray: #2B3836;
  --green: #A9D844;
  --lightgreen: #00AD1B;
  --darkgreen: #396315;
  --darkGrey: #1D2120;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #57CB2C 1.14%, #419E16 100.75%);
  --appColor:  #262F2E;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: black;
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}

.stroke-text{
  color:transparent;
  font-family:Poppins;
  -webkit-text-stroke: 3px var(--lightgreen);
}

.btn{
    padding:0.5rem 2rem;
    border-radius: 0.5rem;
    align-items:center;
    font-size: 1.2rem;
    font-weight: 500;
    font-family:'Poppins';
    cursor: pointer;
    transition: all 0.3s ease;
    width:fit-content;
    background-color: white;
}

.btn:hover{
    background-color: var(--darkGrey);
    border: 3px solid var(--lightgray);
    color: white;
    text-transform: uppercase;
    font-weight:600;
    scale: 1.1;
}

.blur{
  background:rgba(0, 173, 27, 0.69);
  position:absolute;
  border-radius:50%;
  filter:blur(150px);
  z-index:-1;
}

.bm-burger-button{
    display: none;
}

@media (max-width: 768px) {
  .bm-burger-button{
    display: flex;
    width: 2rem;
    height: 1.8rem;
    color: white;
    cursor: pointer;
  }
}

@media (max-width:476px) {
  .bm-burger-button {
    width:1.5rem;
    height:1.6rem;
  }
}

